import React from 'react';
import { useAppContext } from '../libs/contextLib';

export default function Program() {
    
    const { nextYearReady, startDate } = useAppContext();

    return (
        <div id="content">
            <h2>PROGRAM</h2>

            <p>
            Datum konn: {nextYearReady ? startDate : "pro pt ronk zatm nen"}
            </p> 

            <p>
            Msto: <a href="./kontakty">sportovn arel TJ Jistebnk</a>
            </p>     

            <ul>  

                <li>10.00 zahjen dtsk olympidy</li>

                <li>11.00 start maratonu a tafet</li>

                <li>12.45 ukonen sout dtsk olympidy</li>

                <li>13.00 start plmaratonu</li>

                <li>13.15 start 3,6 km (dobh 13.30)</li>

                <li>13.30 start tvrtmaratonu</li>
                
                <li>14.10 vyhlen vsledk dtsk olympidy a bhu na 3,6 km</li>
                
                <li>vyhlen vsledk tvrtmaratonu, plmaratonu a maratonu bude probhat prbn po dobhu a zpracovn vsledk dle jednotlivch zvod</li>
                
                <li>14.30 zahjen doprovodnho programu pro dti (nkter atrakce, nap. mobiln lezeck stna, budou celodenn)</li>

                <li>17.00 veern afterprty, DJ Ivo Bernatk</li>

            </ul>
        </div>
    );
}
